@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@0,400;0,700;0,800;1,400;1,700&display=swap);
@import url(https://rsms.me/inter/inter.css);
:root {
  --colour-background: #fafafa;
  --colour-background-darker: #eee;
  --colour-foreground: #212121;
  --colour-contrast-opacity: 0.8;
  --colour-accent: #3949ab;
  --colour-accent-lighter: #304ffe;
  --font-body-family: "Inter", sans-serif;
  --font-head-family: "Inter var experimental", var(--font-body-family);
  --font-mono-family: "Jetbrains Mono", monospace;
}

body {
  margin: 0;
  background-color: #fafafa;
  background-color: var(--colour-background);
  color: #212121;
  color: var(--colour-foreground);
  font-family: "Inter", sans-serif;
  font-family: var(--font-body-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.4em;

  font-feature-settings: "ss01" 1, "ss03" 1, "cv05" 1, "cv11" 1;
}

a {
  color: #304ffe;
  color: var(--colour-accent-lighter);
  text-decoration: unset;
  border-bottom: 2px solid;
  font-weight: 500;
}

a:hover,
a:focus,
a:active {
  color: #3949ab;
  color: var(--colour-accent);
}

.container {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.hide-small {
  display: none;
}

@media only screen and (min-width: 400px) {
  .container {
    width: 80%;
  }
}

@media only screen and (min-width: 600px) {
  .container {
    width: 60%;
  }

  .hide-small {
    display: inline;
    display: initial;
  }
}

@media only screen and (min-width: 800px) {
  .container {
    width: 50%;
    max-width: 640px;
  }
}

code {
  font-family: "Jetbrains Mono", monospace;
  font-family: var(--font-mono-family);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Inter var experimental", "Inter", sans-serif;
  font-family: var(--font-head-family);
  font-weight: 900;
  font-variation-settings: "slnt" -4.00;
  margin-top: 1em;
  margin-bottom: 0.2em;
}

@media (prefers-color-scheme: dark) {
  :root {
    --colour-background: #1a1a1a;
    --colour-background-darker: #303030;
    --colour-foreground: #fafafa;
    --colour-accent: rgb(255, 255, 0);
    --colour-accent-lighter: rgb(255, 214, 0);
    --colour-contrast-opacity: 0.7;
  }
}

.Footer {
  margin: 1em 0;
  font-size: small;
}

.Navbar {
  display: -webkit-flex;
  display: flex;
  padding-top: 10vh;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  background-color: var(--colour-background-darker);
}

.Navbar .container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.Navbar .navbar-home {
  font-style: unset;
  margin: 0;
  border: 0;
  padding: 8px;
  margin-right: 8px;
  display: block;
  line-height: 0;
}

.Navbar .navbar-title {
  font-family: var(--font-head-family);
  font-weight: 900;
}

.Home {
}

.Home > header {
  display: -webkit-flex;
  display: flex;
  min-height: 40vh;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  background: var(--colour-background-darker);
  padding: 1em 0;
}

.Home .link-stack {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.Home .link-stack a {
  margin: 0.2em;
}

.Home .link-stack a::after {
  content: " →";
}

@media (min-width: 600px) {
  .Home > main > .container {
    display: grid;
    grid-template-columns: auto 16px 25%;
    grid-template-rows: auto;
  }

  .Home .link-stack {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }

  .Home .link-stack a {
    margin: 0.2em 0;
  }
}

.project-button {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0.5em 0;
}

.project-button .project-name {
  font-family: var(--font-head-family);
  margin: 0.2em 0;
}

.project-button .project-name a {
  font-style: normal;
}

.project-button .project-description {
}

.TypewriterHeading {
  font-family: var(--font-mono-family);
  font-weight: 900;
}

.TypewriterHeading-text {
  position: relative;
}

.TypewriterHeading-cover {
  position: absolute;
  top: -0.2em;
  left: 0;
  bottom: -0.2em;
  right: 0;
}

.TypewriterHeading-cover {
  background-color: var(--colour-background-darker);
  -webkit-animation: TypewriterHeading 2.5s steps(var(--textlength), end);
          animation: TypewriterHeading 2.5s steps(var(--textlength), end);
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

@-webkit-keyframes TypewriterHeading {
  to {
    left: 100%;
  }
}

@keyframes TypewriterHeading {
  to {
    left: 100%;
  }
}

@media only screen and (min-width: 600px) {
  .projects {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 16px;
    gap: 16px;
  }
}

@media only screen and (min-width: 1000px) {
  .projects {
    display: grid;
    grid-template-columns: auto auto auto;
  }
}

